import * as React from 'react';
import { connect } from 'react-redux';
import { State, Track, Mix } from '../../types';
import * as audioActions from '../../actions/audio';
import Tracklist from '../Tracklist';
import Player from "../Player/index";
import Mixes from "../Mixes/index";

export type Props = {
    elementId: string;
    mixes: Mix[];
    activeMixes: Mix[];
    activeTracks: Track[]
};

export type AudioViewType = React.SFC<Props>;

export const AudioView: AudioViewType = props => {
    const {
        elementId,
        activeMixes,
        activeTracks
    } = props;

    return (
        <div>
            <Mixes />
            {activeMixes.map(mix =>
                <div key={`active-mix-${mix.id}`}>
                    <h1>{mix.title}</h1>
                    <Player />
                    <div className="peaks">
                        {mix.files
                            .filter(f => /png/i.test(f.format))
                            .map(f => (
                                <img id={`peaks-${elementId}`} key={f.name}
                                    src={`https://archive.org/download/${mix.id}/${f.name}`}
                                />
                            ))
                        }
                    </div>
                    {activeTracks.map(track =>
                        <p key={`active-track-${mix.id}-${track.title}`}>
                            {track.number}{' '}{track.title}
                        </p>
                    )}
                    <Tracklist />
                </div>
            )}
        </div>
    );
};

export const mapState = (state: State) => ({
    elementId: state.audio.elementId,
    activeMixes: state.audio.activeMixes,
    activeTracks: state.audio.activeTracks
});

export default connect(mapState)(AudioView);
